/* style of validity date span */
span::after{
    color:red;
    font-weight: bold;
}
span::after {
    padding-left: 5px;
    }

input:invalid + span::after {
    content: '✖';
    }

input:valid+span::after {
    content: '✓';
    }

/* end style of validity date span */

