 body, html {
	 height: 100%;
	 width: 100%;
	 font-size: 15px;
}
 body {
	 background: #f4f7fd;
	 margin: 0;
	 padding: 10px;
	 font-family: 'Verdana', sans-serif;
	 text-align: center;
	 display: -webkit-flex;
	 display: flex;
	 -webkit-flex-wrap: wrap;
	         flex-wrap: wrap;
	 -webkit-justify-content: center;
	         justify-content: center;
	 -webkit-align-items: center;
	         align-items: center;
	 overflow: scroll;
}
 .wrapper {
	 display: -webkit-flex;
	 display: flex;
	 -webkit-flex-direction: column;
	         flex-direction: column;
	 max-width: 850px;
}
 .header {
	 display: -webkit-flex;
	 display: flex;
	 -webkit-justify-content: space-between;
	         justify-content: space-between;
	 padding: 10px 20px 0;
	 font-family: 'Rowdies', sans-serif;
	 font-size: 1.5rem;
}
 .move-count {
	 color: #8b6af5;
	 line-height: 2.5rem;
}
 button {
	 cursor: pointer;
	 padding: 0.5rem;
	 color: #fff;
	 border-radius: 0.5rem;
	 border: 0;
	 background-color: #8b6af5;
	 font-family: 'Rowdies', sans-serif;
	 font-size: 1.2rem;
	 box-shadow: 5px 5px 10px 0 rgba(90, 116, 148, 0.3);
	 margin-bottom: 1rem;
}
 .container {
	 margin: 0 auto;
	 width: 100%;
}
 .card-wrapper {
	 width: 150px;
	 height: 150px;
	 -webkit-transform-style: preserve-3d;
	         transform-style: preserve-3d;
	 display: inline-block;
	 margin: 0.5rem;
	 -webkit-perspective: 1000px;
	         perspective: 1000px;
	 transition: all 1s ease;
}
 .card-container {
	 width: 100%;
	 height: 100%;
	 position: relative;
	 transition: -webkit-transform 1s;
	 transition: transform 1s;
	 transition: transform 1s, -webkit-transform 1s;
	 -webkit-transform-style: preserve-3d;
	         transform-style: preserve-3d;
}
 .card {
	 background: #fff;
	 color: #555;
	 cursor: pointer;
	 -webkit-user-select: none;
	     -ms-user-select: none;
	         user-select: none;
	 position: absolute;
	 padding: 0.5rem;
	 width: 100%;
	 height: 100%;
	 overflow: hidden;
	 -webkit-backface-visibility: hidden;
	 backface-visibility: hidden;
	 border-radius: 0.5rem;
	 box-shadow: 5px 5px 10px 0 rgba(90, 116, 148, 0.3);
}
 .card svg {
	 width: 100%;
	 height: 100%;
}
 .card-back {
	 background: white;
	 position: absolute;
	 z-index: 1;
}
 .card-front {
	 -webkit-transform: rotateY(180deg);
	         transform: rotateY(180deg);
}
 .is-flipped {
	 -webkit-transform: rotateY(180deg);
	         transform: rotateY(180deg);
}
 .is-matched {
	 -webkit-animation: scale 0.4s cubic-bezier(0.86, 0, 0.07, 1);
	         animation: scale 0.4s cubic-bezier(0.86, 0, 0.07, 1);
	 -webkit-animation-delay: 1s;
	         animation-delay: 1s;
}
 .is-reset {
	 -webkit-animation: scaleBack 0.4s cubic-bezier(0.86, 0, 0.07, 1);
	         animation: scaleBack 0.4s cubic-bezier(0.86, 0, 0.07, 1);
}
 .is-stacked {
	 margin-bottom: -170px;
	 margin-right: -160px;
}
