 #modal {
	 position: fixed;
	 background-color: rgba(57, 57, 91, 0.2);
	 top: 0;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 z-index: 999;
	 pointer-events: none;
	 transition: all 0.3s;
	 display: none;
	 -webkit-align-items: center;
	         align-items: center;
	 -webkit-justify-content: center;
	         justify-content: center;
}
 #modal svg {
	 width: 80%;
	 position: relative;
	 z-index: 99;
}
 #modal button {
	 margin-bottom: 2rem;
	 font-size: 1.5rem;
}
 #modal.show {
	 display: -webkit-flex;
	 display: flex;
	 pointer-events: auto;
}
 #modal h1 {
	 color: #453d84;
	 margin: 0;
}
 #modal h2 {
	 color: #5e5aaf;
	 font-size: 1.2rem;
	 font-weight: normal;
	 margin-bottom: 3rem;
}
 .svg-box {
	 position: relative;
}
 .background-blur {
	 position: absolute;
	 top: 30%;
	 left: 20%;
	 width: 60%;
	 height: 40%;
	 border-radius: 50%;
	 background-color: #fdccfb;
	 -webkit-filter: blur(3rem);
	         filter: blur(3rem);
}
 .modal-box {
	 background-color: #f9f8fe;
	 box-shadow: 5px 5px 20px 0 rgba(69, 61, 132, 0.3);
	 border-radius: 10px;
	 width: 80%;
	 max-width: 500px;
	 text-align: center;
	 padding: 2rem;
	 -webkit-animation: jitter 2.5s forwards ease-in;
	         animation: jitter 2.5s forwards ease-in;
}
 @-webkit-keyframes scale {
	 0%, 100% {
		 -webkit-transform: scale(1) rotateY(180deg);
		         transform: scale(1) rotateY(180deg);
	}
	 50% {
		 -webkit-transform: scale(1.1) rotateY(180deg);
		         transform: scale(1.1) rotateY(180deg);
	}
}
 @keyframes scale {
	 0%, 100% {
		 -webkit-transform: scale(1) rotateY(180deg);
		         transform: scale(1) rotateY(180deg);
	}
	 50% {
		 -webkit-transform: scale(1.1) rotateY(180deg);
		         transform: scale(1.1) rotateY(180deg);
	}
}
 @-webkit-keyframes scaleBack {
	 0%, 100% {
		 -webkit-transform: scale(1);
		         transform: scale(1);
	}
	 50% {
		 -webkit-transform: scale(1.1);
		         transform: scale(1.1);
	}
}
 @keyframes scaleBack {
	 0%, 100% {
		 -webkit-transform: scale(1);
		         transform: scale(1);
	}
	 50% {
		 -webkit-transform: scale(1.1);
		         transform: scale(1.1);
	}
}
 @-webkit-keyframes jitter {
	 0% {
		 -webkit-transform: scale(0);
		         transform: scale(0);
	}
	 10%, 50% {
		 -webkit-transform: scale(1);
		         transform: scale(1);
	}
	 15% {
		 -webkit-transform: scale(0.9);
		         transform: scale(0.9);
	}
	 20% {
		 -webkit-transform: scale(1.1);
		         transform: scale(1.1);
	}
	 25% {
		 -webkit-transform: scale(1.1) rotate(-4deg);
		         transform: scale(1.1) rotate(-4deg);
	}
	 30% {
		 -webkit-transform: scale(1.1) rotate(4deg);
		         transform: scale(1.1) rotate(4deg);
	}
	 35% {
		 -webkit-transform: scale(1.1) rotate(-3deg);
		         transform: scale(1.1) rotate(-3deg);
	}
	 40% {
		 -webkit-transform: scale(1.1) rotate(3deg);
		         transform: scale(1.1) rotate(3deg);
	}
	 45% {
		 -webkit-transform: scale(1.1) rotate(0);
		         transform: scale(1.1) rotate(0);
	}
}
 @keyframes jitter {
	 0% {
		 -webkit-transform: scale(0);
		         transform: scale(0);
	}
	 10%, 50% {
		 -webkit-transform: scale(1);
		         transform: scale(1);
	}
	 15% {
		 -webkit-transform: scale(0.9);
		         transform: scale(0.9);
	}
	 20% {
		 -webkit-transform: scale(1.1);
		         transform: scale(1.1);
	}
	 25% {
		 -webkit-transform: scale(1.1) rotate(-4deg);
		         transform: scale(1.1) rotate(-4deg);
	}
	 30% {
		 -webkit-transform: scale(1.1) rotate(4deg);
		         transform: scale(1.1) rotate(4deg);
	}
	 35% {
		 -webkit-transform: scale(1.1) rotate(-3deg);
		         transform: scale(1.1) rotate(-3deg);
	}
	 40% {
		 -webkit-transform: scale(1.1) rotate(3deg);
		         transform: scale(1.1) rotate(3deg);
	}
	 45% {
		 -webkit-transform: scale(1.1) rotate(0);
		         transform: scale(1.1) rotate(0);
	}
}
